@import "./styles.sass"

@media only screen and (max-width: 1500px)
  .layout
    &.active
      .inner-content
        max-width: 1200px

    .inner-content
      max-width: 1400px

@media only screen and (max-width: 1400px)
  .layout
    &.active
      .inner-content
        max-width: 1100px

    .inner-content
      max-width: 1400px

@media only screen and (max-width: 1300px)
  .layout
    &.active
      .inner-content
        max-width: 1000px

    .inner-content
      max-width: 1400px

  .dashboard
    .list
      @include grid($column: 1fr 1fr 1fr 1fr 1fr)

  .pick-up-orders
    .list
      @include grid($column: 1fr 1fr 1fr 1fr 1fr)

@media only screen and (max-width: 1100px)
  .dashboard
    .list
      @include grid($column: 1fr 1fr 1fr 1fr)

@media only screen and (max-width: 1000px)
  .dashboard
    .list
      @include grid($column: 1fr 1fr 1fr)

  .layout
    &.active
      .side-nav-space
        width: 0

    .side-nav-space
      width: 0px

  .side-nav
    left: -400px
    width: 230px

    .logo
      width: 200px

    .nav
      li
        margin-left: 20px
        @include flex($justify: flex-start)

        &.active
          &::after
            left: -30px

        &::after
          left: -50px

        button
          @include flex($justify: flex-start)

          &:hover
            box-shadow: none

          span
            position: relative
            opacity: 1
            pointer-events: auto

    &.show
      left: 0

    .top-sec
      .side-nav-close
        display: flex

  header
    .side-nav-btn
      display: none

      &.open-close
        display: flex

  .table
    overflow-x: auto
    box-shadow: -4px 0 10px rgba($dark, .1) inset

    table
      width: 1000px

@media only screen and (max-width: 800px)
  .pick-up-orders
    .list
      @include grid($column: 1fr 1fr 1fr 1fr)

  .profile-page
    .inner-section
      .basic-informations
        .inner-details
          .input-groups
            @include grid($column: 1fr 1fr)

      .teams-sections, .payment-sections
        @include grid($column: 1fr 1fr)

  .contact-page
    @include grid($column: 1fr )

@media only screen and (max-width: 600px)
  .dashboard
    .list
      @include grid($column: 1fr 1fr )

  header
    .others
      #headerSearch
        display: none

      #searchPopupBtn
        display: block

  .pick-up-orders
    .list
      @include grid($column: 1fr 1fr 1fr)

@media only screen and (max-width: 500px)
  .pick-up-orders
    .list
      @include grid($column: 1fr 1fr)

  .login-sign-up-page
    .login-form
      h3
        font-size: 20px
      .group
        @include groupInput($size: 40px, $fontSize: 14px)

      .submit-btn
        height: 45px
        font-size: 14px

      .others
        .remember-me
          input
            margin-right: 5px
            height: 15px
            width: 15px

          label
            font-size: 13px

        .forget-password
          font-size: 13px

  .profile-page
    .inner-section
      .personal-details

        .inner-details
          @include flex($justify: flex-start)
          flex-direction: column
          align-items: flex-start

          .user-image
            margin-bottom: 1rem

      .basic-informations
        .inner-details
          .input-groups
            @include grid($column: 1fr 1fr)

      .teams-sections, .payment-sections
        @include grid($column: 1fr)

@media only screen and (max-width: 400px)
  .dashboard
    .list
      @include grid($column: 1fr)

  .pick-up-orders
    .list
      @include grid($column: 1fr)

@media only screen and (max-width: 374px)
  .profile-page
    .inner-section
      .basic-informations
        .inner-details
          .input-groups
            @include grid($column: 1fr)
