@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap')

*
  margin: 0
  padding: 0
  box-sizing: border-box
  font-family: 'Inter', sans-serif
  outline: none
  border: none
  background: none
  border: none
  outline: none
  text-decoration: none
  list-style: none

$main: #0DC2CE
$pri: #595959
$sec: #EDF3F3
$bg: rgba($main, .08)
$error: #A00000
$dark: #141414
$light: #ffffff
$para: #7B7B7B
$icon: #595959

@mixin flex($justify)
  display: flex
  justify-content: $justify
  align-items: center

@mixin grid($column)
  display: grid
  grid-template-columns: $column
  grid-gap: 10px

@mixin imageBg()
  background-position: center
  background-size: cover
  background-repeat: no-repeat

@mixin wrapper()
  max-width: 1300px
  margin: 0 auto
  padding: 0 20px

@mixin trans()
  // transition: 1s cubic-bezier(0,1,0,1)
  transition: .4s

@mixin button()
  padding: .4rem 1rem
  font-size: 14px
  background-color: $pri
  color: $light
  border-radius: 10px
  cursor: pointer

@mixin dark($opacity)
  position: absolute
  top: 0
  left: 0
  right: 0
  bottom: 0
  background-color: rgba(#000000, $opacity)

@mixin bgImage()
  background-position: center
  background-size: cover
  background-repeat: no-repeat

@mixin groupInput($size, $fontSize)
  height: $size
  width: 100%
  position: relative
  margin-top: 2.2rem

  .eyesBtn
    position: absolute
    right: 5px
    top: 50%
    transform: translate(-50%, -50%)
    height: 30px
    width: 30px
    @include flex($justify: center)
    cursor: pointer
    box-shadow: none

    &.active
      &::after
        width: 0
        border: none

    &::after
      content: ''
      position: absolute
      top: 50%
      left: 50%
      height: 3px
      width: 85%
      border: 1px solid $sec
      border-radius: 10px
      background-color: $pri
      transform: translate(-50%, -50%) rotate(45deg)
      @include trans()

    svg
      height: 25px
      width: 25px

      path
        fill: $pri

  &.error
    margin-bottom: 1.6rem

    .error-message
      opacity: 1
      bottom: -20px
      pointer-events: auto

    .form-input-label
      color: $error !important

    input
      background: none
      border-color: $error !important

  .form-input-label
    position: absolute
    top: 50%
    left: 20px
    transform: translateY(-50%)
    font-size: $fontSize
    font-weight: 600
    color: $para
    @include trans()
    pointer-events: none

    &.shrink
      left: 5px
      top: -12px
      color: $main

    &#shrinkActive
      left: 5px
      top: -12px
      color: $main

  .error-message
    @include flex($justify: flex-start)
    color: $error
    font-size: 12px
    position: absolute
    bottom: -10px
    opacity: 0
    left: 5px
    pointer-events: none
    font-weight: 500

    svg
      height: 15px
      width: 15px
      margin-right: 2px

  &#uploadImageBtn
    background-color: $sec
    border-radius: 10px
    @include flex($justify: flex-start)

    h4
      font-size: 16px
      font-weight: 400
      color: $para
      margin-left: 1rem
      pointer-events: none
      opacity: 0

      &.active
        opacity: 1

  input, select, textarea
    height: 100%
    width: 100%
    font-size: 16px
    background-color: $sec
    border-radius: 10px
    @include trans()
    padding: 10px 20px
    border: 1.5px solid $sec

    &.active
      border-color: $main
      background: none

    &::placeholder
      opacity: 0

    &:focus
      border: 1.5px solid $main
      background: none

      &::placeholder
        opacity: 1

    &:focus ~ .form-input-label
      left: 5px
      top: -12px
      color: $main

  textarea
    height: 200px

  select
    color: $para
    font-weight: 600

    &.active
      font-size: 16px
      font-weight: 400
      color: $dark

  .small-select
    font-size: 12px

section
  background-color: $bg

input:hover
  box-shadow: 0 4px 6px rgba($dark, .08)

button
  transition: 400ms cubic-bezier(0,0,0,1)
  cursor: pointer

  &.animate
    transform: scale(0.95)

  &:hover
    box-shadow: 0 4px 10px rgba($dark, .1)

.wrapper
  max-width: 1200px
  padding: 0 20px
  margin: 0 auto

.App
  max-width: 1500px
  margin: 0 auto

/**********   title-section   **********/
.title-section
  @include flex($justify: space-between)
  margin: 2rem 0 1rem

  .title
    margin: 0

  .search-area
    @include flex($justify: flex-end)

    select

  .search
    height: 30px
    width: 200px
    border-radius: 30px
    position: relative
    @include flex($justify: flex-start)

    &.active
      input
        border-color: $main
        background-color: $light

      .search-btn
        opacity: 1
        pointer-events: auto

    .icon
      position: absolute
      height: 30px
      width: 30px
      left: 0
      top: 50%
      transform: translateY(-50%)
      @include flex($justify: center)

      svg
        height: 15px
        height: 15px

    input
      width: 100%
      height: 100%
      padding: 0 25px
      font-size: 14px
      border: 2px solid transparent
      border-radius: 30px
      @include trans()
      background-color: $bg

      &:focus
        border-color: $main
        background-color: $light

      &:focus ~ .search-btn
        opacity: 1
        pointer-events: auto

    .search-btn
      position: absolute
      height: 40px
      width: 40px
      right: 0
      top: 50%
      transform: translateY(-50%)
      border-radius: 50%
      @include flex($justify: center)
      background-color: $main
      opacity: 0
      pointer-events: none
      @include trans()
      z-index: 1

      svg
        height: 20px
        width: 20px
        path
          fill: $light

/**********   login-sign-up-page   **********/
.login-sign-up-page
  @include flex($justify: center)
  flex-direction: column
  min-height: 100vh
  padding: 2rem 0

  .logo
    margin-bottom: 2rem
    width: 180px

    img
      width: 100%

  .message-popup
    position: fixed
    top: 20px
    left: 50%
    transform: translateX(-50%)
    padding: 1rem
    background-color: rgba(#05F000, .22)
    @include flex($justify: center)

    svg
      margin-right: 10px

  .login-form
    max-width: 700px
    background-color: $light
    padding: 1.5rem
    width: 100%
    box-shadow: 0 4px 10px rgba($dark, .1)
    border-radius: 10px

    h3
      font-size: 28px
      margin-bottom: 3rem
      font-weight: 600
      text-align: center

    .switch-login-signup
      font-size: 18px
      text-align: center
      margin-top: 1.5rem

      a
        color: $main
        text-decoration: underline

    .input-groups-section
      overflow: hidden

      .inner-group
        @include flex($justify: flex-start)
        // align-items: flex-start
        transform: translateX(0%)
        @include trans()

      .input-groups
        width: 100%
        margin: 0 .5rem

        h2
          font-size: 18px
          font-weight: 600

    .group
      @include groupInput($size: 60px, $fontSize: 16px)

    .image
      margin-top: 1rem
      height: 0
      width: 100%
      @include flex($justify: center)
      @include trans()

      &.active
        height: 170px

      img
        height: 100%
        width: 100%
        object-fit: contain

    .others
      @include flex($justify: space-between)
      margin-top: 1.2rem

      .remember-me
        @include flex($justify: flex-start)

        input
          margin-right: 10px
          height: 20px
          width: 20px
          border-radius: 5px
          border: 1px solid $main

        label
          font-size: 16px

      .forget-password
        font-size: 16px
        @include trans()

        &:hover
          transform: translateY(-1px) scale(1.04)

        a
          color: $main

    .button-group
      @include flex($justify: center)
      a
        width: 100%

        &:nth-child(1)
          .submit-btn
            margin-right: 10px
            background: none
            color: $main
            border: 1.5px solid $main

        &:nth-child(2)
          .submit-btn
            margin-left: 10px

      button
        &:nth-child(1)
          margin-right: 10px
          background: none
          color: $main
          border: 1.5px solid $main

        &:nth-child(2)
          margin-left: 10px

    .submit-btn
      height: 60px
      width: 100%
      @include flex($justify: center)
      background-color: $main
      color: $light
      border-radius: 10px
      text-align: center
      font-size: 18px
      padding: 1rem
      text-transform: uppercase
      font-weight: 600
      margin-top: 1.2rem

      .timer
        margin-left: 5px

/**********   layout   **********/
.layout
  @include flex($justify: space-between)

  &.active
    .side-nav-space
      width: 280px

    .white-space
      width: 0%

  .header-line
    position: fixed
    top: 100px
    left: 0
    width: 100%
    height: 2px
    background-color: rgba($dark, .2)
    z-index: 10

  .side-nav-space
    width: 120px
    @include trans()

  .all-content
    width: 100%

  .inner-content
    max-width: 1400px
    margin: 0 auto
    padding: 0 20px
    width: 100%

  .white-space
    width: 0
    @include trans()

/**********   side-nav   **********/
.side-nav
  position: fixed
  top: 0
  left: 0
  width: 100px
  height: 100vh
  background-color: $main
  @include flex($justify: flex-strat)
  flex-direction: column
  box-shadow: 0 4px 12px rgba($dark, .15)
  z-index: 11
  @include trans()

  &.active
    width: 230px

    .logo
      width: 200px

    .nav
      li
        margin-left: 20px
        @include flex($justify: flex-start)

        &.active
          &::after
            left: -30px

        &::after
          left: -50px

        button
          @include flex($justify: flex-start)

          &:hover
            box-shadow: none

          span
            position: relative
            opacity: 1
            pointer-events: auto

  .top-sec
    position: relative
    height: 102px
    @include flex($justify: center)
    width: 100%
    border-bottom: 2px solid $light

    .side-nav-close
      position: absolute
      top: 0
      right: -30px
      height: 30px
      width: 30px
      background-color: $error
      @include flex($justify: center)
      display: none
      cursor: pointer

      svg
        path
          fill: $light

  .logo
    height: 70px
    width: 70px
    @include flex($justify: center)

    img
      height: 100%
      width: 100%
      object-fit: contain

  .nav
    margin-top: 2rem
    width: 100%
    overflow: hidden

    li
      padding: 1.2rem 0
      position: relative
      width: 100%
      @include flex($justify: center)
      margin-left: 0

      &.active
        button
          span
            color: $light

          svg
            path
              fill: $light

        &::after
          left: -10px
          background-color: $light

      &::after
        content: ''
        position: absolute
        top: 50%
        left: -30px
        transform: translateY(-50%) rotate(45deg)
        height: 20px
        width: 20px
        border-radius: 6px
        background-color: none
        @include trans()
        z-index: 1

      button
        @include flex($justify: center)
        @include trans()

        &:hover
          box-shadow: none

        span
          font-size: 16px
          font-weight: 600
          opacity: 0
          pointer-events: none
          position: absolute
          // color: rgba($light, .8)
          color: #d2f7ff

        svg
          height: 30px
          width: 30px
          margin-right: 5px

          path
            // fill: rgba($light, .8)
            fill: #d2f7ff
            @include trans()

.title
  font-size: 20px
  margin: 2rem 0 18px

/**********   dashboard   **********/
.dashboard
  .list
    @include grid($column: 1fr 1fr 1fr 1fr 1fr 1fr)

    a
      border-radius: 10px
      @include trans()
      @include flex($justify: center)

      &:hover
        box-shadow: 0 4px 10px rgba($dark, .1)

.dashboard-card
  padding: 1rem
  @include flex($justify: flex-start)
  flex-direction: column
  width: 100%
  text-align: center
  border-radius: 10px
  color: $dark

  .icon
    svg
      height: 50px
      width: 50px

  .name
    font-size: 15px
    font-weight: 600
    margin: .5rem 0

  h3
    font-weight: 300
    font-size: 20px

/**********   profile-button   **********/
.profile-button
  margin-left: 1rem
  cursor: pointer

  img
    height: 60px
    width: 60px
    object-fit: cover
    border-radius: 50%

  &:hover
    .profile-option-popup
      top: 70px
      opacity: 1
      pointer-events: auto

  .profile-option-popup
    position: absolute
    top: 60px
    right: 0%
    padding-top: 10px
    z-index: 2
    opacity: 0
    pointer-events: none
    @include trans()

    ul
      padding: .5rem
      border-radius: 10px
      width: 180px
      background-color: $light
      box-shadow: 0 4px 10px rgba($dark, .1)

    li
      margin-bottom: 5px
      width: 100%

      &:nth-last-child(1)
        button

          &:hover
            color: $error

            svg
              path
                fill: $error

      button
        @include flex($justify: flex-start)
        font-size: 14px
        padding: .5rem
        width: 100%
        border-radius: 6px
        font-weight: 500

        &:hover
          // background-color: rgba($dark, .1)
          background-color: $bg

        svg
          height: 15px
          width: 15px
          margin-right: 5px

          path
            fill: $icon

/**********   header   **********/
header
  position: sticky
  top: 0
  height: 100px
  background-color: $light
  z-index: 10

  .inner
    height: 100%
    @include flex($justify: space-between)
    max-width: 1400px
    padding: 0 20px
    margin: 0 auto

  .side-nav-btn
    height: 50px
    width: 60px
    border-radius: 10px
    background-color: $bg
    @include flex($justify: center)
    padding: 15px 8px

    &.open-close
      display: none

    .arrow
      // transform: translateX(-5px) rotateY(180deg)
      transform: translateX(0px) rotateY(0)
      @include flex($justify: center)
      @include trans()

      &.rotate
        transform: translateX(-5px) rotateY(180deg)

      svg
        height: 20px
        width: 20px

    .bars
      width: 100%
      height: 100%
      @include flex($justify: flex-start)
      position: relative

      &::before
        content: ''
        position: absolute
        top: 0
        left: 50%
        transform: translate(-50%, -50%)
        height: 3.5px
        width: 100%
        border-radius: 10px
        background-color: $dark

      .bar
        position: relative
        height: 3.5px
        width: 70%
        border-radius: 10px
        background-color: $dark

      &::after
        content: ''
        position: absolute
        top: 100%
        left: 50%
        transform: translate(-50%, -50%)
        height: 3.5px
        width: 100%
        border-radius: 10px
        background-color: $dark

  .others
    @include flex($justify: flex-end)

    #searchPopupBtn
      display: none

      button
        svg
          path
            fill: $icon

    .search
      height: 40px
      width: 300px
      border-radius: 30px
      position: relative
      @include flex($justify: flex-start)

      &.active
        input
          border-color: $main
          background-color: $light

        .search-btn
          opacity: 1
          pointer-events: auto

      .icon
        position: absolute
        height: 40px
        width: 40px
        left: 0
        top: 50%
        transform: translateY(-50%)
        @include flex($justify: center)

        svg
          height: 20px
          height: 20px

      input
        width: 100%
        height: 100%
        padding: 0 35px
        font-size: 16px
        border: 2px solid transparent
        border-radius: 30px
        @include trans()
        background-color: $bg

        &:focus
          border-color: $main
          background-color: $light

        &:focus ~ .search-btn
          opacity: 1
          pointer-events: auto

      .search-btn
        position: absolute
        height: 40px
        width: 40px
        right: 0
        top: 50%
        transform: translateY(-50%)
        border-radius: 50%
        @include flex($justify: center)
        background-color: $main
        opacity: 0
        pointer-events: none
        @include trans()
        z-index: 1

        svg
          height: 20px
          width: 20px
          path
            fill: $light

    .useful-button
      @include flex($justify: flex-end)

      button
        height: 40px
        width: 40px
        @include flex($justify: center)
        background-color: $bg
        border-radius: 50%
        margin-left: 1rem

        svg
          height: 25px
          width: 25px

/**********   table   **********/
table
  width: 100%
  border-radius: 10px
  overflow: hidden
  border: 0
  box-shadow: 0 4px 10px rgba($dark, .1)
  margin-bottom: 2rem

  thead
    tr
      background-color: $main

      th
        text-transform: uppercase
        font-size: 14px
        color: $light
        padding: 1rem .5rem
        text-align: left

        &:nth-child(1)
          text-align: center

        &.center
          text-align: center

  tbody
    tr
      &:nth-last-child(1)
        td
          border-bottom: none

      &.total
        td
          font-weight: 700
          border-bottom: none

      td
        padding: 1.2rem .5rem
        color: #373737
        font-size: 14px
        border-bottom: 1px solid #c2c2c2

        .contactNo
          margin-bottom: 5px

          &:nth-last-child(1)
            margin-bottom: 0

        h4
          padding: .4rem .6rem
          color: $light
          border-radius: 10px
          font-size: 13px
          font-weight: 500
          display: inline-block

          &.Delivered
            background-color: rgba(#0EAF00, .7)

          &.Cancelled
            background-color: rgba(#AF0000, .7)

          &.Pending
            background-color: rgba(#F2AE00, .7)

          &.Adjustments
            background-color: rgba(#0066AF, .7)

          &.Problematic
            background-color: rgba(#AC00AF, .7)

          &.Courier
            background-color: rgba(#00AFAF, .7)

          &.Rescheduled
            background-color: rgba(#AF8900, .7)

        &:nth-child(1)
          text-align: center

        select
          height: 35px
          width: 100%
          border-radius: 10px
          border: 2px solid $main

        .button-group
          @include flex($justify: flex-strat)

          button, .btn
            height: 30px
            width: 30px
            border-radius: 10px
            background-color: #595959
            @include flex($justify: center)
            cursor: pointer

            svg
              height: 20px
              width: 20px

            &.delete-btn
              background-color: #B00101
              margin-left: 10px

/**********   profile-page   **********/
.profile-page
  margin-bottom: 2rem

  .inner-section
    padding: 1rem
    border-radius: 10px
    background-color: $bg

    .sec-title
      font-size: 16px
      font-weight: 700
      margin-bottom: .5rem

    .personal-details
      margin-bottom: 1.5rem

      .inner-details
        @include flex($justify: flex-start)

        .user-image
          margin-right: 10px

          .image-upload
            position: relative
            height: 120px
            width: 120px
            border-radius: 10px
            margin-top: 1.2rem

            &:hover
              button
                pointer-events: auto
                opacity: 1

            .image-div
              height: 100%
              width: 100%

              img
                height: 100%
                width: 100%
                object-fit: cover
                border-radius: 50%

            button
              position: absolute
              top: 0
              left: 0
              height: 100%
              width: 100%
              border-radius: 50%
              @include flex($justify: center)
              flex-direction: column
              pointer-events: none
              opacity: 0
              @include trans()
              z-index: 1

              svg
                margin-bottom: 10px
                height: 40px
                width: 40px

                path
                  fill: $light

              span
                color: $light
                font-weight: 600
                font-size: 12px
                text-shadow: 0 2px 4px rgba($dark, 2)

        .other-info
          .name
            font-size: 14px

          p
            font-size: 13px
            margin-top: 3px

          .button-group
            margin-top: .8rem
            @include flex($justify: flex-start)

            button
              margin-right: 10px
              padding: .4rem .6rem
              border-radius: 6px
              background-color: $main
              border: 1px solid $main
              color: $light
              font-weight: 600
              font-size: 12px

              &:nth-last-child(1)
                background: none
                border: 1px solid $main
                color: $main

    .group
      @include groupInput($size: 40px, $fontSize: 12px)
      margin-top: 1.3rem

      input
        font-size: 14px
        padding: 0 .6rem

    .submit
      padding: 1rem 1.5rem
      background-color: $main
      color: $light
      border-radius: 10px
      box-shadow: 0 2px 4px rgba($dark, .2)
      text-transform: uppercase
      font-size: 14px
      font-weight: 600

    .basic-informations
      margin-bottom: 1rem

      .sec-title
        margin-bottom: .2rem

      .inner-details
        .input-groups
          @include grid($column: 1fr 1fr 1fr)
          grid-row-gap: 0

    .teams-sections, .payment-sections
      margin-bottom: 1rem
      @include grid($column: 1fr 1fr 1fr)

      .owner, .manager, .assigned-person
        margin-top: 1rem

        .inner-details
          .image-upload
            position: relative
            height: 170px
            width: 170px
            border-radius: 10px
            border: 2px dashed $main
            margin-top: 1rem

            .image-div
              height: 100%
              width: 100%

              img
                height: 100%
                width: 100%
                object-fit: cover

            button
              position: absolute
              top: 0
              left: 0
              height: 100%
              width: 100%
              @include flex($justify: center)
              flex-direction: column
              z-index: 1

              svg
                margin-bottom: 10px

                path
                  fill: $icon

              span
                color: $icon
                font-weight: 600
                font-size: 13px

              &.active
                svg
                  path
                    fill: $light

                span
                  color: $light
                  text-shadow: 0 2px 10px rgba($dark, 1)

/**********   pickup-request   **********/
.pickup-request
  margin-bottom: 3rem

/**********   pick-up-orders   **********/
.pick-up-orders
  .list
    @include grid($column: 1fr 1fr 1fr 1fr 1fr 1fr)

    .item
      padding: .7rem
      border-radius: 10px
      @include flex($justify: center)
      align-items: flex-start
      flex-direction: column

      &:nth-last-child(1)
        button
          background-color: #CE0D0D

          svg
            path
              fill: #CE0D0D

      .name
        font-size: 13px

      p
        font-size: 20px
        font-weight: 300
        margin: 1rem 0

      button
        @include flex($justify: center)
        padding: .3rem .5rem
        background-color: $main
        color: $light
        border-radius: 10px
        font-size: 11px
        font-weight: 600

        svg
          height: 15px
          width: 15px
          margin-right: 5px

/**********   popup-bg   **********/
.popup-bg
  position: fixed
  top: 0
  left: 0
  height: 100%
  width: 100%
  background-color: rgba($dark, .5)
  backdrop-filter: blur(5px)
  z-index: 19
  opacity: 0
  pointer-events: none
  @include trans()

  &.active
    opacity: 1
    pointer-events: auto

/**********   popup   **********/
.popup
  position: fixed
  top: 50%
  left: 50%
  max-width: 300px
  width: 100%
  transform: translate(-50%, -50%) scale(1.1)
  background-color: $light
  padding: 1rem
  border-radius: 10px
  box-shadow: 0 4px 10px rgba($dark, .1)
  z-index: 20
  opacity: 0
  pointer-events: none
  @include trans()
  transition-delay: .1s

  &.no-padding
    padding: 0

    .title
      margin-top: 1rem

  &.active
    opacity: 1
    transform: translate(-50%, -50%) scale(1)
    pointer-events: auto

  .close-btn
    position: absolute
    top: -5px
    right: -5px
    height: 30px
    width: 30px
    background-color: $para
    @include flex($justify: center)
    border-radius: 50%
    border: 2px solid $light

    svg
      path
        fill: $light

  .title
    font-size: 18px
    font-weight: 600
    text-align: center
    margin: 0

/**********   edit-profile-popup   **********/
.edit-profile-popup
  .group
    @include groupInput($size: 40px, $fontSize: 12px)
    margin-top: 1.3rem

    input
      font-size: 14px
      padding: 0 .6rem
  button
    padding: .6rem 1rem
    background-color: $main
    color: $light
    font-weight: 600
    text-transform: uppercase
    border-radius: 10px
    margin-top: 1rem
    font-size: 12px

/**********   cash-collection   **********/
.cash-collection
  .total-price-to-collect
    margin-top: 1rem
    padding: 1rem
    width: 100%
    border-radius: 10px
    background-color: $main
    color: $light
    @include flex($justify: space-between)

/**********   search-popup   **********/
.search-popup
  position: fixed
  top: 0
  left: 0
  height: 100%
  width: 100%
  z-index: 100
  pointer-events: none

  &.active
    pointer-events: auto

    .search-bg
      top: 30%
      left: 30%
      height: 2000px
      width: 2000px
      opacity: 1

    .search-popup-close
      opacity: 1

    .search-field
      opacity: 1

  .search-bg
    position: absolute
    top: 0
    left: 0
    transform: translate(-50%, -50%)
    height: 0
    width: 0
    border-radius: 50%
    background-color: #141414
    opacity: 0
    @include trans()

  .search-popup-close
    position: absolute
    top: 10px
    right: 10px
    @include flex($justify: center)
    z-index: 1
    opacity: 0

    svg
      height: 50px
      width: 50px
      path
        fill: $light

  .search-field
    position: relative
    max-width: 550px
    margin: 0 auto
    padding: 20px
    margin-top: 4rem
    border-bottom: 2px solid rgba($light, .4)
    height: 80px
    opacity: 0

    .line
      position: absolute
      bottom: -2px
      left: 50%
      transform: translateX(-50%)
      background-color: $light
      height: 2px
      width: 0
      @include trans()

      &.active
        width: 100%

    input
      width: 100%
      font-size: 30px
      color: $light
      height: 100%

    button
      position: absolute
      right: 10px
      top: 50%
      transform: translateY(-50%)
      height: 60px
      width: 60px
      border-radius: 50%
      @include flex($justify: center)

      svg
        height: 40px
        width: 40px

        path
          fill: rgba($light, .4)

      &.active
        svg
          path
            fill: $light

/**********   confirm-delete-btns & confirm-btns   **********/
.confirm-delete-btns, .confirm-btns
  margin-top: 1rem
  @include flex($justify: space-evenly)

  button
    border: 1px solid $dark
    width: 100%
    padding: 1rem 0
    border-bottom: none
    border-left: none

    &:hover
      background-color: rgba($dark, .1)

    &:nth-last-child(1)
      border-right: none
      border-radius: 0 0 10px 0

      &:hover
        background-color: $error
        color: $light

/**********   confirm-btns   **********/
.confirm-btns
  button
    &:hover
      background-color: rgba($dark, .1)

    &:nth-last-child(1)
      border-right: none
      border-radius: 0 0 10px 0

      &:hover
        background-color: $main
        color: $light

/**********   pie-charts   **********/
.pie-charts
  @include flex($justify: space-evenly)
  flex-wrap: wrap

/**********   contact-page   **********/
.contact-page
  @include grid($column: 1fr 1fr)

  .contact-form
    .group
      @include groupInput($size: 50px, $fontSize: 15px)

      &.text-area
        @include groupInput($size: 200px, $fontSize: 15px)

        .form-input-label
          top: 25px

    button
      font-size: 16px
      padding: 1rem 2rem
      background-color: $main
      color: $light
      margin: 1rem 0 2rem
      border-radius: 10px

  .contact-informations
    ul
      li
        margin-bottom: 10px
        @include flex($justify: flex-start)
        font-size: 16px

        strong
          margin-right: 5px

        .icon
          margin-right: 5px
          svg
            height: 18px
            width: 18px

        button
          @include flex($justify: flex-start)
          font-size: 16px
          box-shadow: none

          &:hover
            color: $main

            .icon
              svg
                path
                  fill: $main

/**********    notification-page    **********/
.notifications
  padding: 1rem
  border-radius: 20px
  background-color: $bg
  margin-top: 1rem

  .title
    margin-bottom: 1rem
    margin-top: 5px

  .notifications-list
    @include flex($justify: flex-start)
    flex-direction: column

    .item
      @include grid($column: .1fr 5fr)
      padding: 1.5rem 0
      align-items: flex-start
      border-bottom: 1px solid rgba($dark, .2)

      &:nth-last-child(1)
        border-bottom: none

      .icon
        height: 40px
        width: 40px
        border-radius: 50%
        background-color: rgba($main, .3)
        @include flex($justify: center)

        svg
          width: 60%
          path
            fill: $main

      .details
        .desc
          font-size: 18px
          color: $para
          line-height: 1.7rem
          margin-bottom: .6rem
          text-align: justify

        .date
          font-size: 14px
          color: rgba($dark, .5)
          font-weight: 400

.popup-messaegs
  position: fixed
  top: -60px
  left: 50%
  transform: translateX(-50%)
  background-color: rgba(#04db00, .3)
  border-radius: 10px
  font-size: 14px
  text-align: left
  @include flex($justify: start)
  max-width: 400px
  padding: 1rem
  color: $dark
  z-index: 100
  transition: .4s cubic-bezier(0, 1, 0, 1)
  opacity: 0
  box-shadow: 0 4px 10px rgba($dark, .2)

  &::after
    content: ''
    position: absolute
    left: 0
    top: 0
    height: 100%
    width: 6px
    background-color: #039f00
    border-radius: 10px 0 0 10px

  .message
    h3
      font-size: 14px

  svg
    margin-right: 10px

  &.active
    top: 20px
    opacity: 1

.chat
  @include flex($justify: flex-start)
  margin-bottom: 1rem
  width: 100%
  cursor: pointer

  .userImage
    height: 60px
    width: 60px
    margin-right: 10px

    img
      height: 100%
      width: 100%
      border-radius: 50%
      object-fit: cover

  .other
    max-width: 400px
    width: 100%

    .userName
      @include flex($justify: space-between)
      margin-bottom: 2px

      h3
        font-size: 18px
        color: rgba($dark, .8)

      .status, .status-off
        font-size: 12px
        font-weight: 600
        color: #059c00

      .status-off
        color: rgba($dark, .6)

    .lastMessage
      font-size: 14px
      color: rgba($dark, .8)

.letter-popup
  position: fixed
  bottom: -500px
  right: 10px
  height: 400px
  width: 300px
  box-shadow: 0 4px 10px rgba($dark, .1)
  @include flex($justify: center)
  align-items: flex-start
  flex-direction: column
  border-radius: 10px
  transition: .4s cubic-bezier(0, 1, 0, 1)

  &.active
    bottom: 5px

  .letter-head
    width: 100%
    height: 12%
    padding: .4rem
    @include flex($justify: space-between)
    background-color: $main
    border-radius: 10px 10px 0 0

    .image
      @include flex($justify: flex-start)

      img
        height: 35px
        width: 35px
        border-radius: 50%
        object-fit: cover

      .others
        margin-left: 5px

        .name
          font-weight: 500
          font-size: 14px
          color: $light

        .status
          font-size: 12px
          font-weight: 500
          color: rgba($light, .8)

    .letter-popup-close
      @include flex($justify: center)

      &:hover
        background-color: rgba($dark, .1)
        border-radius: 50%

      svg
        path
          fill: $light

  .letter-body
    height: 100%
    overflow-y: scroll
    width: 100%
    background-color: $light
    padding: 6px 10px

    .inner
      @include flex($justify: flex-end)
      align-items: flex-end
      flex-direction: column

    .other-letter
      width: 100%
      @include flex($justify: flex-start)
      align-items: flex-end
      margin-top: 10px

      img
        height: 25px
        width: 25px
        border-radius: 50%
        margin-right: 4px

      .letter-box
        font-size: 14px
        padding: .4rem
        background-color: rgba($dark, .1)
        border-radius: 10px 10px 10px 0
        max-width: 190px

    .your-letter
      width: 100%
      @include flex($justify: flex-end)
      margin-top: 10px

      .letter-box
        font-size: 14px
        padding: .4rem
        background-color: rgba($main, .2)
        border-radius: 10px 10px 0 10px
        max-width: 190px

  .letter-footer
    width: 100%
    @include flex($justify: flex-end)
    align-items: flex-start
    flex-direction: column

    form
      width: 100%

    .letter-input
      width: 100%
      height: 40px
      background-color: #eee
      border-radius: 0 0 10px 10px
      position: relative

      input
        height: 100%
        width: 100%
        padding: 0 65px 0 10px
        border-radius: 10px

      button
        position: absolute
        top: 50%
        right: 5px
        transform: translateY(-50%)
        text-transform: uppercase
        background-color: $main
        color: $light
        font-weight: 600
        padding: .5rem
        border-radius: 6px

.loading
  height: 100vh
  width: 100%
  @include flex($justify: center)
